async function bindleftCommonSearchdropdown(instance,modulename, filtername) {    
    var vm = this;
    if(filtername=='Status'){
        var url = `type=KnowledgeBase`;
        DataServices.GetDDLData(url).then(response => {
            if (response.data.DATA.length > 0) {
                vm.leftsearchSchema[6].listOptions = [];
                var StatusData = response.data.DATA;
                StatusData.forEach(element => {
                    var Obj = {
                        name: element.name,
                        value: element.value
                    }                  
                    vm.leftsearchSchema[6].listOptions.push(Obj);
                });
            }
        })
    }
    if(filtername=='Category'){        
            DataServices.GetCategoryDDL().then(response => {
                if (response.data.result.length > 0) {
                    var CategortData = response.data.result;
                    vm.leftsearchSchema[1].listOptions = [];
                    CategortData.forEach(element => {
                        if (element.TICKET_ARTICLE_CATEGORY_PARENT == null) {
                            let obj = {
                                name: element.TICKET_ARTICLE_CATEGORY_NAME,
                                value: element.TICKET_ARTICLE_CATEGORY_ID
                            }                           
                            vm.leftsearchSchema[1].listOptions.push(obj);
                        }
                        // if (element.TICKET_ARTICLE_CATEGORY_PARENT == element.TICKET_ARTICLE_CATEGORY_PARENT) {
                        //     let obj = {
                        //         name: element.TICKET_ARTICLE_CATEGORY_NAME,
                        //         value: element.TICKET_ARTICLE_CATEGORY_ID
                        //     }                            
                        //     vm.leftsearchSchema[1].listOptions.push(obj);
                        // }
                    });
                }
            })
    }
    if(filtername=='Users'){
        var url = `moduleName=KnowledgeBase`;
            DataServices.GetUsersByCompanyId(url).then(response => {
                if (JSON.parse(response.data.result).length > 0) {
                    vm.leftsearchSchema[2].listOptions = [];
                    var StatusData = JSON.parse(response.data.result);
                    StatusData.forEach(element => {
                        var Obj = {
                            name: element.USERNAME,
                            value: element.USER_ID
                        }
                        vm.leftsearchSchema[2].listOptions.push(Obj);
                    });
                }
            })
    }    
} 

export default { buttons, leftsearchSchema,listActions ,bindleftCommonSearchdropdown};