<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{performAction }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">                    
                        <dynamic-form ref="AddTransfer" lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">                            
                        </dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props:{
        performAction:{
            type:String,
            required:true
        },
        articleIds:{
            type:String,
            required:true
        }
    },
    data(){
        return{
            FormSchema: [
                {
                    layoutType: "single",
                    Data: [                        
                        {
                            astype: "SelectField",
                            label: this.$t('User'),
                            name: "User",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('User'),
                            validationRules: { "required": true},
                        }                                           
                    ]
                }                              
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Submit",                   
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close
                }
            ]
        }
    },
    created:function(){        
        this.GetUsersByCompanyId();
    },
    methods:{
        GetUsersByCompanyId: function () {
            var url = `moduleName=KnowledgeBase`;
            DataServices.GetUsersByCompanyId(url).then(response => {
                //debugger;

                if (JSON.parse(response.data.result).length > 0) {
                    var StatusData = JSON.parse(response.data.result);
                    StatusData.forEach(element => {
                        var Obj = {
                            name: element.USERNAME,
                            value: element.USER_ID
                        }
                        this.FormSchema[0].Data[0].config.options.push(Obj);
                    });


                }
                this.$refs.AddTransfer.UpdateKeyValue();
            })
        },
        Close:function(){
            this.$parent.IsTransfer=false;
        },
        onSubmit(formfields, val) {            
            var vm=this;
            if(formfields.Category !=''){
            var params = `categoryId=${formfields.User}&articleIds=${vm.articleIds}&performAction=${vm.performAction}`;
            DataServices.CopyandMoveArticles(params).then(response=>{                
                if(response.data.result=='1'){
                    if (vm.performAction == "TRANSFAR"){
                        vm.ShowAlert(vm.$t('TransferSuccess', [vm.$t('Article')]), "Success", true, vm.$t('Alert'));
                    }
                    vm.$parent.FetchData();
                    vm.Close();
                }
            })
            }else{
                vm.ShowAlert(vm.$t('SelectCategoryFirst'), "warning", true, vm.$t('Alert'));
            }
        }
    }
}
</script>