<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{performAction }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">                    
                        <dynamic-form ref="AddMove" lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">                            
                        </dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props:{
        performAction:{
            type:String,
            required:true
        },
        articleIds:{
            type:String,
            required:true
        }
    },
    data(){
        return{
            FormSchema: [
                {
                    layoutType: "single",
                    Data: [                        
                        {
                            astype: "SelectField",
                            label: this.$t('Category'),
                            name: "Category",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Category'),
                            validationRules: { "required": true},
                        }                                           
                    ]
                }                              
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Submit",                   
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close
                }
            ]
        }
    },
    created:function(){        
        this.GetCategory();
    },
    methods:{
        GetCategory:function(){
            var vm=this;
            DataServices.GetCategoryDDL().then(response=>{
                if(response.data.result.length>0){
                    var CategortData=response.data.result;
                    vm.FormSchema[0].Data[0].config.options=[];
                    var CategortDataMain=CategortData.find(x=>x.TICKET_ARTICLE_CATEGORY_PARENT==null)
                    CategortData.forEach(element => {
                        if(element.TICKET_ARTICLE_CATEGORY_PARENT==null)
                        {
                            let obj={
                                name:element.TICKET_ARTICLE_CATEGORY_NAME,
                                value:element.TICKET_ARTICLE_CATEGORY_ID
                            }
                            vm.FormSchema[0].Data[0].config.options.push(obj);
                        }
                        // if(element.TICKET_ARTICLE_CATEGORY_PARENT==element.TICKET_ARTICLE_CATEGORY_PARENT)
                        // {
                        //     let obj={
                        //         name:element.TICKET_ARTICLE_CATEGORY_NAME,
                        //         value:element.TICKET_ARTICLE_CATEGORY_ID
                        //     }
                        //     vm.FormSchema[0].Data[0].config.options.push(obj);
                        // }
                    });
                }            
                this.$refs.AddMove.UpdateKeyValue();   
            })
        },
        Close:function(){
            this.$parent.IsMoveCopy=false;
        },
        onSubmit(formfields, val) {
            debugger
            var vm=this;            
            let obj={
                categoryId:formfields.Category,
                articleIds:vm.articleIds,
                performAction:vm.performAction
            }
            if(formfields.Category !=''){
            var params = `categoryId=${formfields.Category}&articleIds=${vm.articleIds}&performAction=${vm.performAction}`;
            DataServices.CopyandMoveArticles(params).then(response=>{
                debugger
                if(response.data.result=='1'){
                    if (vm.performAction == "Move"){
                        vm.ShowAlert(vm.$t('ArticleMoved', [vm.$t('Article')]), "Success", true, vm.$t('Alert'));
                    }else{
                        vm.ShowAlert(vm.$t('Articlecopied', [vm.$t('Article')]), "Success", true, vm.$t('Alert'));
                    }
                    vm.$parent.FetchData();
                    vm.Close();
                }
            })
            }else{
                vm.ShowAlert(vm.$t('SelectCategoryFirst'), "warning", true, vm.$t('Alert'));
            }
        }
    }
}
</script>